import React from "react";
import { MdOutlineFamilyRestroom } from "react-icons/md";
import { MdChildFriendly } from "react-icons/md";
import { GiSyringe } from "react-icons/gi";

const ManagementBtn = () => {
  return (
    <div className="flex flex-row justify-center items-center gap-52 flex-wrap">
      <div
        className="flex flex-col items-center gap-2 transform transition-transform duration-200 hover:scale-105 active:scale-95 cursor-pointer"
        onClick={() => alert("Parent Management clicked")}
        role="button"
        tabIndex={0}
        aria-label="Parent Management"
      >
        <MdOutlineFamilyRestroom size={150} color="pink" />
        <p className="text-center font-medium">Parent Management</p>
      </div>

      <div
        className="flex flex-col items-center gap-2 transform transition-transform duration-200 hover:scale-105 active:scale-95 cursor-pointer"
        onClick={() => alert("Infant Management clicked")}
        role="button"
        tabIndex={0}
        aria-label="Infant Management"
      >
        <MdChildFriendly size={150} color="lightgreen" />
        <p className="text-center font-medium">Infant Management</p>
      </div>

      <div
        className="flex flex-col items-center gap-2 transform transition-transform duration-200 hover:scale-105 active:scale-95 cursor-pointer"
        onClick={() => alert("Vaccine Management clicked")}
        role="button"
        tabIndex={0}
        aria-label="Vaccine Management"
      >
        <GiSyringe size={150} color="lightblue" />
        <p className="text-center font-medium">Vaccine Management</p>
      </div>
    </div>
  );
};

export default ManagementBtn;
