"use client";

import { useProtectedRoutesApi } from "@/libraries/API/ProtectedRoute/secureRoutes";
import { useQuery } from "@tanstack/react-query";
import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  ResponsiveContainer,
} from "recharts";
import { useMemo } from "react";
import ManagementBtn from "./managementBtn";

// Array of distinct colors
const colors = [
  "#8884d8",
  "#82ca9d",
  "#ff7300",
  "#ff0000",
  "#00c49f",
  "#a4de6c",
  "#d0ed57",
  "#ffbb28",
  "#d0b9f2", // Add more colors as needed
];

interface DataPoint {
  vaccineName: string;
  [key: string]: number | string; // Dynamic keys for infant names
}

export default function VaccineGraph() {
  const { vaccinePercentageRoutes } = useProtectedRoutesApi();

  const {
    data: percentage,
    isLoading: isPercentageLoading,
    isError: isPercentageError,
    error: percentageError,
  } = useQuery({
    queryKey: ["percentage"],
    queryFn: vaccinePercentageRoutes,
  });

  // Memoize the transformed data to avoid unnecessary recalculations
  const transformedData = useMemo(() => {
    if (!percentage?.data?.infants) return [];

    return percentage.data.infants[0]?.vaccinationSchedule.map(
      (vaccine, index) => {
        const dataPoint: DataPoint = { vaccineName: vaccine.vaccineName };
        percentage.data.infants.forEach((infant) => {
          if (infant.vaccinationSchedule[index]) {
            dataPoint[infant.fullname] =
              infant.vaccinationSchedule[index].percentage || 0;
          }
        });
        return dataPoint;
      }
    );
  }, [percentage]);

  if (isPercentageError) {
    return (
      <div>
        <p>Error: {percentageError?.message || "An unknown error occurred"}</p>
      </div>
    );
  }

  if (isPercentageLoading) {
    return <p>Loading...</p>;
  }

  return (
    <>
      <h1 className="text-xl font-bold">Vaccination Coverage</h1>
      <div className="w-full h-[400px]">
        <ResponsiveContainer width="100%" height="100%">
          <BarChart
            data={transformedData}
            margin={{ top: 20, right: 30, left: 20, bottom: 60 }}
          >
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis
              dataKey="vaccineName"
              textAnchor="middle" // Align the text horizontally
              tick={{ fontSize: 12 }} // Adjust font size
              interval={0} // Show all ticks
              dy={20} // Adjust vertical position of labels
            />
            <YAxis domain={[0, 100]} tickFormatter={(tick) => `${tick}%`} />
            <Tooltip />

            {percentage?.data?.infants?.map((infant, idx) => (
              <Bar
                key={infant.id || idx}
                dataKey={infant.fullname}
                fill={colors[idx % colors.length]}
                barSize={30}
              />
            ))}
          </BarChart>
        </ResponsiveContainer>

        <ManagementBtn />
      </div>
    </>
  );
}
