import React, { useState } from "react";
import LogoutBtn from "./logoutBtn";

const Sidebar = () => {
  const [selectedTab, setSelectedTab] = useState(window.location.pathname);

  const handleTabClick = (path: React.SetStateAction<string>) => {
    setSelectedTab(path);
    // Add your navigation logic here if needed
  };

  return (
    <aside className="bg-gray-900 text-white p-6 h-full sticky top-0">
      <p className="mb-20">Admin Dashboard</p>
      <nav className="flex flex-col gap-2">
        <button
          onClick={() => handleTabClick("/home")}
          className={`px-4 py-2 rounded hover:bg-gray-800 transition-colors duration-200 text-left ${
            selectedTab === "/home"
              ? "bg-gray-800 text-blue-400 font-semibold"
              : ""
          }`}
        >
          Home
        </button>

        <button
          onClick={() => handleTabClick("/about")}
          className={`px-4 py-2 rounded hover:bg-gray-800 transition-colors duration-200 text-left ${
            selectedTab === "/about"
              ? "bg-gray-800 text-blue-400 font-semibold"
              : ""
          }`}
        >
          About
        </button>

        <button
          onClick={() => handleTabClick("/services")}
          className={`px-4 py-2 rounded hover:bg-gray-800 transition-colors duration-200 text-left ${
            selectedTab === "/services"
              ? "bg-gray-800 text-blue-400 font-semibold"
              : ""
          }`}
        >
          Services
        </button>

        <button
          onClick={() => handleTabClick("/contact")}
          className={`px-4 py-2 rounded hover:bg-gray-800 transition-colors duration-200 text-left ${
            selectedTab === "/contact"
              ? "bg-gray-800 text-blue-400 font-semibold"
              : ""
          }`}
        >
          Contact
        </button>
      </nav>

      <div className="mt-10 align-middle">
        <LogoutBtn />
      </div>
    </aside>
  );
};

export default Sidebar;
